import React from 'react';
import { Sample, User } from '../types';
import { Beaker, AlertTriangle, CheckCircle, Clock, Activity, TrendingUp } from 'lucide-react';

interface DashboardProps {
  samples: Sample[];
  auditLog: any[];
  user: User;
}

const Dashboard: React.FC<DashboardProps> = ({ samples, auditLog, user }) => {
  const activeSamples = samples.filter(s => s.status === 'active').length;
  const quarantineSamples = samples.filter(s => s.status === 'quarantine').length;
  const disposedSamples = samples.filter(s => s.status === 'disposed').length;
  const recentActivities = auditLog.slice(-5).reverse();

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'text-green-600 bg-green-100';
      case 'quarantine': return 'text-yellow-600 bg-yellow-100';
      case 'disposed': return 'text-gray-600 bg-gray-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'active': return <CheckCircle className="h-4 w-4" />;
      case 'quarantine': return <AlertTriangle className="h-4 w-4" />;
      case 'disposed': return <Clock className="h-4 w-4" />;
      default: return <Clock className="h-4 w-4" />;
    }
  };

  return (
    <div className="space-y-8">
      {/* Welcome Message */}
      <div className="bg-gradient-to-r from-blue-600 to-blue-700 rounded-2xl shadow-xl p-8 text-white">
        <div className="flex items-center space-x-4">
          <div className="bg-white/20 p-3 rounded-xl">
            <TrendingUp className="h-8 w-8" />
          </div>
          <div>
            <h2 className="text-3xl font-bold mb-2">
              Bem-vindo ao GuardLab, {user.name}
            </h2>
            <p className="text-blue-100 text-lg">
              Sistema profissional de gestão laboratorial com controle avançado de amostras biológicas
            </p>
          </div>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100 hover:shadow-xl transition-shadow duration-200">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="bg-blue-100 p-3 rounded-xl">
                <Beaker className="h-8 w-8 text-blue-600" />
              </div>
            </div>
            <div className="ml-4">
              <p className="text-sm font-semibold text-gray-500 uppercase tracking-wide">Total de Amostras</p>
              <p className="text-3xl font-bold text-gray-900">{samples.length}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100 hover:shadow-xl transition-shadow duration-200">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="bg-green-100 p-3 rounded-xl">
                <CheckCircle className="h-8 w-8 text-green-600" />
              </div>
            </div>
            <div className="ml-4">
              <p className="text-sm font-semibold text-gray-500 uppercase tracking-wide">Ativas</p>
              <p className="text-3xl font-bold text-gray-900">{activeSamples}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100 hover:shadow-xl transition-shadow duration-200">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="bg-yellow-100 p-3 rounded-xl">
                <AlertTriangle className="h-8 w-8 text-yellow-600" />
              </div>
            </div>
            <div className="ml-4">
              <p className="text-sm font-semibold text-gray-500 uppercase tracking-wide">Quarentena</p>
              <p className="text-3xl font-bold text-gray-900">{quarantineSamples}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100 hover:shadow-xl transition-shadow duration-200">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="bg-gray-100 p-3 rounded-xl">
                <Clock className="h-8 w-8 text-gray-600" />
              </div>
            </div>
            <div className="ml-4">
              <p className="text-sm font-semibold text-gray-500 uppercase tracking-wide">Descartadas</p>
              <p className="text-3xl font-bold text-gray-900">{disposedSamples}</p>
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Recent Samples */}
        <div className="bg-white rounded-xl shadow-lg border border-gray-100">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-semibold text-gray-900">Amostras Recentes</h3>
          </div>
          <div className="p-6">
            {samples.slice(-5).reverse().map((sample) => (
              <div key={sample.id} className="flex items-center justify-between py-4 border-b border-gray-100 last:border-b-0">
                <div>
                  <p className="text-sm font-semibold text-gray-900">{sample.code}</p>
                  <p className="text-xs text-gray-500">{sample.type}</p>
                </div>
                <div className="flex items-center space-x-2">
                  <span className={`inline-flex items-center px-3 py-1 rounded-full text-xs font-semibold ${getStatusColor(sample.status)}`}>
                    {getStatusIcon(sample.status)}
                    <span className="ml-1 capitalize">{sample.status}</span>
                  </span>
                </div>
              </div>
            ))}
            {samples.length === 0 && (
              <p className="text-gray-500 text-center py-8">Nenhuma amostra cadastrada</p>
            )}
          </div>
        </div>

        {/* Recent Activities */}
        <div className="bg-white rounded-xl shadow-lg border border-gray-100">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <Activity className="h-5 w-5 mr-2 text-blue-600" />
              Atividades Recentes
            </h3>
          </div>
          <div className="p-6">
            {recentActivities.map((activity, index) => (
              <div key={index} className="flex items-start space-x-3 py-4 border-b border-gray-100 last:border-b-0">
                <div className="flex-shrink-0">
                  <div className="w-3 h-3 bg-blue-600 rounded-full mt-2"></div>
                </div>
                <div className="flex-1">
                  <p className="text-sm font-medium text-gray-900">{activity.action}</p>
                  <p className="text-xs text-gray-500 mt-1">
                    {activity.user} • {new Date(activity.timestamp).toLocaleString('pt-BR')}
                  </p>
                </div>
              </div>
            ))}
            {auditLog.length === 0 && (
              <p className="text-gray-500 text-center py-8">Nenhuma atividade registrada</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
