import React, { useState } from 'react';
import { Sample, User, AuditEntry } from '../types';
import { BarChart3, Download, Calendar, TrendingUp, TrendingDown, Activity } from 'lucide-react';

interface ReportsProps {
  samples: Sample[];
  auditLog: AuditEntry[];
  user: User;
}

const Reports: React.FC<ReportsProps> = ({ samples, auditLog, user }) => {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [reportType, setReportType] = useState<'entries' | 'status-changes' | 'summary'>('summary');

  const filterSamplesByDateRange = (samples: Sample[]) => {
    if (!startDate || !endDate) return samples;
    
    return samples.filter(sample => {
      const storageDate = new Date(sample.storageDate);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return storageDate >= start && storageDate <= end;
    });
  };

  const filterAuditByDateRange = (auditLog: AuditEntry[]) => {
    if (!startDate || !endDate) return auditLog;
    
    return auditLog.filter(entry => {
      const entryDate = new Date(entry.timestamp);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return entryDate >= start && entryDate <= end;
    });
  };

  const filteredSamples = filterSamplesByDateRange(samples);
  const filteredAudit = filterAuditByDateRange(auditLog);

  const getEntriesInPeriod = () => {
    return filteredSamples.filter(sample => 
      sample.createdAt >= (startDate ? new Date(startDate).toISOString() : '1900-01-01') &&
      sample.createdAt <= (endDate ? new Date(endDate + 'T23:59:59').toISOString() : '2100-01-01')
    );
  };

  const getStatusChangesInPeriod = () => {
    return filteredAudit.filter(entry => 
      entry.action.includes('atualizada') || 
      entry.action.includes('status') ||
      entry.action.includes('removida')
    );
  };

  const exportToCSV = () => {
    let csvContent = '';
    let filename = '';

    if (reportType === 'entries') {
      const entries = getEntriesInPeriod();
      csvContent = 'Código,Tipo,Origem,Paciente,Data Coleta,Data Armazenamento,Status,Risco,Local,Criado Por\n';
      entries.forEach(sample => {
        csvContent += `${sample.code},${sample.type},${sample.origin},${sample.patientName || ''},${sample.collectionDate},${sample.storageDate},${sample.status},${sample.riskLevel},${sample.storageLocation},${sample.createdBy}\n`;
      });
      filename = `entradas_${startDate}_${endDate}.csv`;
    } else if (reportType === 'status-changes') {
      const changes = getStatusChangesInPeriod();
      csvContent = 'Data,Ação,Usuário,Código Amostra\n';
      changes.forEach(entry => {
        csvContent += `${new Date(entry.timestamp).toLocaleString('pt-BR')},${entry.action},${entry.user},${entry.sampleCode || ''}\n`;
      });
      filename = `mudancas_status_${startDate}_${endDate}.csv`;
    } else {
      csvContent = 'Métrica,Valor\n';
      csvContent += `Total de Amostras no Período,${filteredSamples.length}\n`;
      csvContent += `Amostras Ativas,${filteredSamples.filter(s => s.status === 'active').length}\n`;
      csvContent += `Amostras em Quarentena,${filteredSamples.filter(s => s.status === 'quarantine').length}\n`;
      csvContent += `Amostras Descartadas,${filteredSamples.filter(s => s.status === 'disposed').length}\n`;
      csvContent += `Novas Entradas,${getEntriesInPeriod().length}\n`;
      csvContent += `Mudanças de Status,${getStatusChangesInPeriod().length}\n`;
      filename = `resumo_${startDate}_${endDate}.csv`;
    }

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', filename);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const entries = getEntriesInPeriod();
  const statusChanges = getStatusChangesInPeriod();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white rounded-lg shadow p-6">
        <div className="flex items-center space-x-3 mb-4">
          <BarChart3 className="h-6 w-6 text-blue-600" />
          <h2 className="text-xl font-semibold text-gray-900">Relatórios de Amostras</h2>
        </div>
        <p className="text-gray-600">
          Análise detalhada de entradas e saídas de amostras por período
        </p>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg shadow p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">Filtros do Relatório</h3>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Data Inicial
            </label>
            <div className="relative">
              <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <input
                type="date"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
                className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Data Final
            </label>
            <div className="relative">
              <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <input
                type="date"
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
                className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Tipo de Relatório
            </label>
            <select
              value={reportType}
              onChange={(e) => setReportType(e.target.value as any)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="summary">Resumo</option>
              <option value="entries">Entradas</option>
              <option value="status-changes">Mudanças de Status</option>
            </select>
          </div>

          <div className="flex items-end">
            <button
              onClick={exportToCSV}
              className="w-full flex items-center justify-center px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 transition-colors"
            >
              <Download className="h-4 w-4 mr-2" />
              Exportar CSV
            </button>
          </div>
        </div>
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <TrendingUp className="h-8 w-8 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Amostras no Período</p>
              <p className="text-2xl font-semibold text-gray-900">{filteredSamples.length}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <Activity className="h-8 w-8 text-green-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Novas Entradas</p>
              <p className="text-2xl font-semibold text-gray-900">{entries.length}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <TrendingDown className="h-8 w-8 text-orange-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Mudanças de Status</p>
              <p className="text-2xl font-semibold text-gray-900">{statusChanges.length}</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <BarChart3 className="h-8 w-8 text-purple-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Atividades Totais</p>
              <p className="text-2xl font-semibold text-gray-900">{filteredAudit.length}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Report Content */}
      {reportType === 'summary' && (
        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">Resumo do Período</h3>
          </div>
          <div className="p-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h4 className="text-md font-medium text-gray-900 mb-3">Status das Amostras</h4>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Ativas:</span>
                    <span className="text-sm font-medium text-green-600">
                      {filteredSamples.filter(s => s.status === 'active').length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Quarentena:</span>
                    <span className="text-sm font-medium text-yellow-600">
                      {filteredSamples.filter(s => s.status === 'quarantine').length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Descartadas:</span>
                    <span className="text-sm font-medium text-gray-600">
                      {filteredSamples.filter(s => s.status === 'disposed').length}
                    </span>
                  </div>
                </div>
              </div>

              <div>
                <h4 className="text-md font-medium text-gray-900 mb-3">Nível de Risco</h4>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Baixo:</span>
                    <span className="text-sm font-medium text-green-600">
                      {filteredSamples.filter(s => s.riskLevel === 'low').length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Médio:</span>
                    <span className="text-sm font-medium text-yellow-600">
                      {filteredSamples.filter(s => s.riskLevel === 'medium').length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Alto:</span>
                    <span className="text-sm font-medium text-red-600">
                      {filteredSamples.filter(s => s.riskLevel === 'high').length}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {reportType === 'entries' && (
        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">Entradas no Período</h3>
          </div>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Código
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Tipo
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Data Armazenamento
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Criado Por
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {entries.map((sample) => (
                  <tr key={sample.id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {sample.code}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {sample.type}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {new Date(sample.storageDate).toLocaleDateString('pt-BR')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`inline-flex px-2 py-1 rounded-full text-xs font-medium ${
                        sample.status === 'active' ? 'text-green-700 bg-green-100' :
                        sample.status === 'quarantine' ? 'text-yellow-700 bg-yellow-100' :
                        'text-gray-700 bg-gray-100'
                      }`}>
                        {sample.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {sample.createdBy}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            {entries.length === 0 && (
              <div className="text-center py-12">
                <p className="text-gray-500">Nenhuma entrada encontrada no período</p>
              </div>
            )}
          </div>
        </div>
      )}

      {reportType === 'status-changes' && (
        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">Mudanças de Status no Período</h3>
          </div>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Data/Hora
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Ação
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Usuário
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Código Amostra
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {statusChanges.map((entry) => (
                  <tr key={entry.id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {new Date(entry.timestamp).toLocaleString('pt-BR')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {entry.action}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {entry.user}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {entry.sampleCode || '-'}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            {statusChanges.length === 0 && (
              <div className="text-center py-12">
                <p className="text-gray-500">Nenhuma mudança de status encontrada no período</p>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default Reports;
