import React, { useState } from 'react';
import { User } from '../types';
import { Shield, Eye, EyeOff, Lock } from 'lucide-react';
import { ASSETS } from '../assetManifest';

interface LoginProps {
  onLogin: (user: User) => void;
}

const Login: React.FC<LoginProps> = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  // Demo users for the system
  const demoUsers = [
    { 
      id: '1', 
      username: 'admin', 
      password: 'admin123', 
      name: 'Admin', 
      role: 'admin' as const,
      email: 'admin@guardlab.com',
      phone: '(11) 99999-0001',
      birthDate: '1980-01-01',
      workplace: 'GuardLab Central',
      hasPassword: true
    },
    { 
      id: '2', 
      username: 'researcher', 
      password: 'research123', 
      name: 'Maria Santos', 
      role: 'researcher' as const,
      email: 'maria.santos@guardlab.com',
      phone: '(11) 99999-0002',
      birthDate: '1985-05-15',
      workplace: 'Instituto de Pesquisa',
      hasPassword: true
    },
    { 
      id: '3', 
      username: 'technician', 
      password: 'tech123', 
      name: 'João Oliveira', 
      role: 'technician' as const,
      email: 'joao.oliveira@guardlab.com',
      phone: '(11) 99999-0003',
      birthDate: '1990-12-10',
      workplace: 'GuardLab Central',
      hasPassword: true
    }
  ];

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    // Simulate authentication delay
    await new Promise(resolve => setTimeout(resolve, 1000));

    const user = demoUsers.find(u => u.username === username && u.password === password);
    
    if (user) {
      const userSession = {
        id: user.id,
        username: user.username,
        name: user.name,
        email: user.email,
        phone: user.phone,
        birthDate: user.birthDate,
        workplace: user.workplace,
        role: user.role,
        loginTime: new Date().toISOString(),
        hasPassword: user.hasPassword
      };
      
      localStorage.setItem('nb3_user', JSON.stringify(userSession));
      onLogin(userSession);
    } else {
      setError('Credenciais inválidas. Verifique usuário e senha.');
    }
    
    setLoading(false);
  };

  return (
    <div 
      className="min-h-screen flex items-center justify-center px-4 relative"
      style={{
        backgroundImage: `url(${ASSETS.backgrounds.loginBackground.path})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat'
      }}
    >
      {/* Overlay */}
      <div className="absolute inset-0 bg-gradient-to-br from-slate-900/90 via-blue-900/80 to-slate-900/90"></div>
      
      <div className="max-w-md w-full relative z-10">
        {/* Logo and Title */}
        <div className="text-center mb-8">
          <div className="flex justify-center mb-6">
            <div className="bg-white/10 p-4 rounded-2xl backdrop-blur-sm border border-white/20">
              <Shield 
                className="h-16 w-16 text-white"
              />
            </div>
          </div>
          <h1 className="text-4xl font-bold text-white mb-3 tracking-tight">GuardLab</h1>
          <p className="text-blue-200 text-lg font-medium">Sistema de Gestão Laboratorial</p>
          <p className="text-blue-300 text-sm mt-2">Segurança e Controle de Amostras Biológicas</p>
        </div>

        {/* Login Form */}
        <div className="bg-white/95 backdrop-blur-sm rounded-2xl shadow-2xl p-8 border border-white/20">
          <div className="flex items-center justify-center mb-6">
            <div className="bg-blue-100 p-3 rounded-full">
              <Lock className="h-6 w-6 text-blue-600" />
            </div>
          </div>
          
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="username" className="block text-sm font-semibold text-gray-700 mb-2">
                Usuário
              </label>
              <input
                id="username"
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                placeholder="Digite seu usuário"
                required
              />
            </div>

            <div>
              <label htmlFor="password" className="block text-sm font-semibold text-gray-700 mb-2">
                Senha
              </label>
              <div className="relative">
                <input
                  id="password"
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                  placeholder="Digite sua senha"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute inset-y-0 right-0 pr-4 flex items-center"
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5 text-gray-400 hover:text-gray-600" />
                  ) : (
                    <Eye className="h-5 w-5 text-gray-400 hover:text-gray-600" />
                  )}
                </button>
              </div>
            </div>

            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-xl text-sm font-medium">
                {error}
              </div>
            )}

            <button
              type="submit"
              disabled={loading}
              className="w-full bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 disabled:from-blue-400 disabled:to-blue-500 text-white font-semibold py-3 px-4 rounded-xl transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 shadow-lg"
            >
              {loading ? (
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                  Entrando...
                </div>
              ) : (
                'Entrar no Sistema'
              )}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
