import React, { useState } from 'react';
import { Sample, User } from '../types';
import { saveToStorage, addAuditEntry } from '../utils/storage';
import { CONSTANTS } from '../data';
import { Save, X } from 'lucide-react';

interface SampleManagerProps {
  samples: Sample[];
  setSamples: (samples: Sample[]) => void;
  setAuditLog: (log: any[]) => void;
  user: User;
  onSuccess: () => void;
  editingSample?: Sample;
}

const SampleManager: React.FC<SampleManagerProps> = ({ 
  samples, 
  setSamples, 
  setAuditLog, 
  user, 
  onSuccess,
  editingSample 
}) => {
  const [formData, setFormData] = useState({
    code: editingSample?.code || '',
    type: editingSample?.type || '',
    origin: editingSample?.origin || '',
    patientName: editingSample?.patientName || '',
    collectionDate: editingSample?.collectionDate || '',
    storageDate: editingSample?.storageDate || new Date().toISOString().split('T')[0],
    description: editingSample?.description || '',
    riskLevel: editingSample?.riskLevel || 'medium',
    status: editingSample?.status || 'active',
    storageLocation: editingSample?.storageLocation || '',
    temperature: editingSample?.temperature || '',
    volume: editingSample?.volume || '',
    concentration: editingSample?.concentration || '',
    notes: editingSample?.notes || ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.code.trim()) {
      newErrors.code = 'Código da amostra é obrigatório';
    } else if (samples.some(s => s.code === formData.code && s.id !== editingSample?.id)) {
      newErrors.code = 'Código já existe';
    }

    if (!formData.type.trim()) {
      newErrors.type = 'Tipo da amostra é obrigatório';
    }

    if (!formData.origin.trim()) {
      newErrors.origin = 'Origem é obrigatória';
    }

    if (!formData.collectionDate) {
      newErrors.collectionDate = 'Data de coleta é obrigatória';
    }

    if (!formData.storageDate) {
      newErrors.storageDate = 'Data de armazenamento é obrigatória';
    }

    if (!formData.storageLocation.trim()) {
      newErrors.storageLocation = 'Local de armazenamento é obrigatório';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    const sampleData: Sample = {
      id: editingSample?.id || Date.now().toString(),
      ...formData,
      createdBy: editingSample?.createdBy || user.username,
      createdAt: editingSample?.createdAt || new Date().toISOString(),
      updatedBy: user.username,
      updatedAt: new Date().toISOString()
    };

    let newSamples;
    let action;

    if (editingSample) {
      newSamples = samples.map(s => s.id === editingSample.id ? sampleData : s);
      action = `Amostra ${sampleData.code} atualizada`;
    } else {
      newSamples = [...samples, sampleData];
      action = `Nova amostra ${sampleData.code} criada`;
    }

    setSamples(newSamples);
    saveToStorage('nb3_samples', newSamples);
    
    const auditEntry = addAuditEntry(action, user.username, sampleData.code);
    setAuditLog(prev => [...prev, auditEntry]);

    onSuccess();
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  return (
    <div className="bg-white rounded-lg shadow">
      <div className="px-6 py-4 border-b border-gray-200">
        <h2 className="text-xl font-semibold text-gray-900">
          {editingSample ? 'Editar Amostra' : 'Nova Amostra'}
        </h2>
      </div>

      <form onSubmit={handleSubmit} className="p-6 space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {/* Basic Information */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-900">Informações Básicas</h3>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Código da Amostra *
              </label>
              <input
                type="text"
                value={formData.code}
                onChange={(e) => handleInputChange('code', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.code ? 'border-red-300' : 'border-gray-300'
                }`}
                placeholder="Ex: NB3-2024-001"
              />
              {errors.code && <p className="text-red-500 text-xs mt-1">{errors.code}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Tipo da Amostra *
              </label>
              <select
                value={formData.type}
                onChange={(e) => handleInputChange('type', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.type ? 'border-red-300' : 'border-gray-300'
                }`}
              >
                <option value="">Selecione o tipo</option>
                {CONSTANTS.SAMPLE_TYPES.map(type => (
                  <option key={type} value={type}>{type}</option>
                ))}
              </select>
              {errors.type && <p className="text-red-500 text-xs mt-1">{errors.type}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Origem *
              </label>
              <input
                type="text"
                value={formData.origin}
                onChange={(e) => handleInputChange('origin', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.origin ? 'border-red-300' : 'border-gray-300'
                }`}
                placeholder="Ex: Paciente, Ambiente, Cultura"
              />
              {errors.origin && <p className="text-red-500 text-xs mt-1">{errors.origin}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Nome do Paciente
              </label>
              <input
                type="text"
                value={formData.patientName}
                onChange={(e) => handleInputChange('patientName', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Ex: João Silva"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Data de Coleta *
              </label>
              <input
                type="date"
                value={formData.collectionDate}
                onChange={(e) => handleInputChange('collectionDate', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.collectionDate ? 'border-red-300' : 'border-gray-300'
                }`}
              />
              {errors.collectionDate && <p className="text-red-500 text-xs mt-1">{errors.collectionDate}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Data de Armazenamento *
              </label>
              <input
                type="date"
                value={formData.storageDate}
                onChange={(e) => handleInputChange('storageDate', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.storageDate ? 'border-red-300' : 'border-gray-300'
                }`}
              />
              {errors.storageDate && <p className="text-red-500 text-xs mt-1">{errors.storageDate}</p>}
            </div>
          </div>

          {/* Storage and Safety Information */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-900">Armazenamento e Segurança</h3>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Nível de Risco
              </label>
              <select
                value={formData.riskLevel}
                onChange={(e) => handleInputChange('riskLevel', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                {CONSTANTS.RISK_LEVELS.map(level => (
                  <option key={level.value} value={level.value}>{level.label}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Status
              </label>
              <select
                value={formData.status}
                onChange={(e) => handleInputChange('status', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                {CONSTANTS.SAMPLE_STATUS.map(status => (
                  <option key={status.value} value={status.value}>{status.label}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Local de Armazenamento *
              </label>
              <input
                type="text"
                value={formData.storageLocation}
                onChange={(e) => handleInputChange('storageLocation', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.storageLocation ? 'border-red-300' : 'border-gray-300'
                }`}
                placeholder="Ex: Freezer A1, Prateleira B2"
              />
              {errors.storageLocation && <p className="text-red-500 text-xs mt-1">{errors.storageLocation}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Temperatura (°C)
              </label>
              <input
                type="text"
                value={formData.temperature}
                onChange={(e) => handleInputChange('temperature', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Ex: -80, 4, 25"
              />
            </div>
          </div>
        </div>

        {/* Additional Information */}
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Informações Adicionais</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Volume
              </label>
              <input
                type="text"
                value={formData.volume}
                onChange={(e) => handleInputChange('volume', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Ex: 1ml, 500μl"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Concentração
              </label>
              <input
                type="text"
                value={formData.concentration}
                onChange={(e) => handleInputChange('concentration', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Ex: 10^6 CFU/ml"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Descrição
            </label>
            <textarea
              value={formData.description}
              onChange={(e) => handleInputChange('description', e.target.value)}
              rows={3}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Descrição detalhada da amostra"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Observações
            </label>
            <textarea
              value={formData.notes}
              onChange={(e) => handleInputChange('notes', e.target.value)}
              rows={2}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Observações adicionais"
            />
          </div>
        </div>

        {/* Form Actions */}
        <div className="flex justify-end space-x-3 pt-6 border-t border-gray-200">
          <button
            type="button"
            onClick={onSuccess}
            className="flex items-center px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition-colors"
          >
            <X className="h-4 w-4 mr-2" />
            Cancelar
          </button>
          <button
            type="submit"
            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
          >
            <Save className="h-4 w-4 mr-2" />
            {editingSample ? 'Atualizar' : 'Salvar'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default SampleManager;
