import { User, Sample, AuditEntry } from '../types';

export const saveToStorage = (key: string, data: any) => {
  try {
    localStorage.setItem(key, JSON.stringify(data));
  } catch (error) {
    console.error('Error saving to localStorage:', error);
  }
};

export const getFromStorage = (key: string, defaultValue: any = null) => {
  try {
    const item = localStorage.getItem(key);
    return item ? JSON.parse(item) : defaultValue;
  } catch (error) {
    console.error('Error reading from localStorage:', error);
    return defaultValue;
  }
};

export const getStoredUser = (): User | null => {
  return getFromStorage('nb3_user', null);
};

export const getStoredSamples = (): Sample[] => {
  return getFromStorage('nb3_samples', []);
};

export const getStoredAuditLog = (): AuditEntry[] => {
  return getFromStorage('nb3_audit_log', []);
};

export const getStoredUsers = (): User[] => {
  return getFromStorage('nb3_all_users', []);
};

export const addAuditEntry = (action: string, user: string, sampleCode?: string): AuditEntry => {
  const entry: AuditEntry = {
    id: Date.now().toString(),
    action,
    user,
    timestamp: new Date().toISOString(),
    sampleCode
  };

  const currentLog = getStoredAuditLog();
  const newLog = [...currentLog, entry];
  saveToStorage('nb3_audit_log', newLog);

  return entry;
};
