import React, { useState } from 'react';
import { Search, Calendar, User, Activity } from 'lucide-react';

interface AuditLogProps {
  auditLog: any[];
}

const AuditLog: React.FC<AuditLogProps> = ({ auditLog }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [dateFilter, setDateFilter] = useState('');

  const filteredLog = auditLog.filter(entry => {
    const matchesSearch = entry.action.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         entry.user.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         (entry.sampleCode && entry.sampleCode.toLowerCase().includes(searchTerm.toLowerCase()));
    
    const matchesDate = !dateFilter || 
                       new Date(entry.timestamp).toDateString() === new Date(dateFilter).toDateString();
    
    return matchesSearch && matchesDate;
  }).reverse(); // Show most recent first

  const getActionIcon = (action: string) => {
    if (action.includes('criada') || action.includes('Nova')) {
      return <div className="w-2 h-2 bg-green-500 rounded-full"></div>;
    } else if (action.includes('atualizada') || action.includes('Editar')) {
      return <div className="w-2 h-2 bg-blue-500 rounded-full"></div>;
    } else if (action.includes('removida') || action.includes('excluída')) {
      return <div className="w-2 h-2 bg-red-500 rounded-full"></div>;
    } else if (action.includes('login') || action.includes('Login')) {
      return <div className="w-2 h-2 bg-purple-500 rounded-full"></div>;
    }
    return <div className="w-2 h-2 bg-gray-500 rounded-full"></div>;
  };

  return (
    <div className="bg-white rounded-lg shadow">
      <div className="px-6 py-4 border-b border-gray-200">
        <div className="flex items-center space-x-3">
          <Activity className="h-6 w-6 text-blue-600" />
          <h2 className="text-xl font-semibold text-gray-900">Log de Auditoria</h2>
        </div>
        <p className="text-gray-600 text-sm mt-1">
          Rastreamento completo de todas as atividades do sistema
        </p>
      </div>

      {/* Filters */}
      <div className="p-6 border-b border-gray-200">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
            <input
              type="text"
              placeholder="Buscar por ação, usuário ou código da amostra..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="relative">
            <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
            <input
              type="date"
              value={dateFilter}
              onChange={(e) => setDateFilter(e.target.value)}
              className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
        </div>
      </div>

      {/* Audit Log Entries */}
      <div className="p-6">
        {filteredLog.length > 0 ? (
          <div className="space-y-4">
            {filteredLog.map((entry, index) => (
              <div key={index} className="flex items-start space-x-4 p-4 bg-gray-50 rounded-lg">
                <div className="flex-shrink-0 mt-1">
                  {getActionIcon(entry.action)}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between">
                    <p className="text-sm font-medium text-gray-900">
                      {entry.action}
                    </p>
                    <p className="text-xs text-gray-500">
                      {new Date(entry.timestamp).toLocaleString('pt-BR')}
                    </p>
                  </div>
                  <div className="mt-1 flex items-center space-x-4 text-xs text-gray-500">
                    <div className="flex items-center space-x-1">
                      <User className="h-3 w-3" />
                      <span>{entry.user}</span>
                    </div>
                    {entry.sampleCode && (
                      <div className="flex items-center space-x-1">
                        <span>Amostra: {entry.sampleCode}</span>
                      </div>
                    )}
                    <div>
                      ID: {entry.id}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <Activity className="h-12 w-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-500">Nenhuma atividade encontrada</p>
            {searchTerm || dateFilter ? (
              <p className="text-gray-400 text-sm mt-2">
                Tente ajustar os filtros de busca
              </p>
            ) : null}
          </div>
        )}
      </div>

      {/* Statistics */}
      {auditLog.length > 0 && (
        <div className="px-6 py-4 border-t border-gray-200 bg-gray-50">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
            <div>
              <p className="text-2xl font-semibold text-gray-900">{auditLog.length}</p>
              <p className="text-sm text-gray-600">Total de Atividades</p>
            </div>
            <div>
              <p className="text-2xl font-semibold text-gray-900">
                {new Set(auditLog.map(entry => entry.user)).size}
              </p>
              <p className="text-sm text-gray-600">Usuários Ativos</p>
            </div>
            <div>
              <p className="text-2xl font-semibold text-gray-900">
                {auditLog.filter(entry => 
                  new Date(entry.timestamp).toDateString() === new Date().toDateString()
                ).length}
              </p>
              <p className="text-sm text-gray-600">Atividades Hoje</p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AuditLog;
