import React, { useState, useEffect } from 'react';
import Login from './components/Login';
import Dashboard from './components/Dashboard';
import SampleManager from './components/SampleManager';
import SampleList from './components/SampleList';
import AuditLog from './components/AuditLog';
import Reports from './components/Reports';
import UserManager from './components/UserManager';
import { User, Sample } from './types';
import { getStoredUser, getStoredSamples, getStoredAuditLog } from './utils/storage';
import { Shield, Beaker, FileText, List, LogOut, BarChart3, Users } from 'lucide-react';

function App() {
  const [user, setUser] = useState<User | null>(null);
  const [currentView, setCurrentView] = useState<'dashboard' | 'samples' | 'add-sample' | 'audit' | 'reports' | 'users'>('dashboard');
  const [samples, setSamples] = useState<Sample[]>([]);
  const [auditLog, setAuditLog] = useState<any[]>([]);

  useEffect(() => {
    const storedUser = getStoredUser();
    if (storedUser) {
      setUser(storedUser);
    }
    
    setSamples(getStoredSamples());
    setAuditLog(getStoredAuditLog());
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('nb3_user');
    setUser(null);
    setCurrentView('dashboard');
  };

  if (!user) {
    return <Login onLogin={setUser} />;
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-gradient-to-r from-slate-900 via-blue-900 to-slate-900 text-white shadow-xl">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-3">
                <div className="bg-white/10 p-2 rounded-lg backdrop-blur-sm">
                  <Shield className="h-8 w-8 text-blue-300" />
                </div>
                <div>
                  <h1 className="text-2xl font-bold tracking-tight">GuardLab</h1>
                  <p className="text-blue-200 text-sm font-medium">Sistema de Gestão Laboratorial</p>
                </div>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              <div className="text-right">
                <p className="text-blue-200 text-sm">Bem-vindo,</p>
                <p className="text-white font-medium">{user.name}</p>
              </div>
              <button
                onClick={handleLogout}
                className="flex items-center space-x-2 bg-white/10 hover:bg-white/20 px-4 py-2 rounded-lg transition-all duration-200 backdrop-blur-sm border border-white/20"
              >
                <LogOut className="h-4 w-4" />
                <span>Sair</span>
              </button>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Navigation */}
        <nav className="mb-8">
          <div className="flex space-x-2 bg-white rounded-xl p-2 shadow-sm border border-gray-200">
            <button
              onClick={() => setCurrentView('dashboard')}
              className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                currentView === 'dashboard'
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-100'
              }`}
            >
              <Beaker className="h-4 w-4" />
              <span>Dashboard</span>
            </button>
            <button
              onClick={() => setCurrentView('samples')}
              className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                currentView === 'samples'
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-100'
              }`}
            >
              <List className="h-4 w-4" />
              <span>Amostras</span>
            </button>
            <button
              onClick={() => setCurrentView('add-sample')}
              className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                currentView === 'add-sample'
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-100'
              }`}
            >
              <Beaker className="h-4 w-4" />
              <span>Nova Amostra</span>
            </button>
            <button
              onClick={() => setCurrentView('reports')}
              className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                currentView === 'reports'
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-100'
              }`}
            >
              <BarChart3 className="h-4 w-4" />
              <span>Relatórios</span>
            </button>
            <button
              onClick={() => setCurrentView('audit')}
              className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                currentView === 'audit'
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-100'
              }`}
            >
              <FileText className="h-4 w-4" />
              <span>Auditoria</span>
            </button>
            {user.role === 'admin' && (
              <button
                onClick={() => setCurrentView('users')}
                className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-200 font-medium ${
                  currentView === 'users'
                    ? 'bg-blue-600 text-white shadow-md'
                    : 'text-gray-700 hover:bg-gray-100'
                }`}
              >
                <Users className="h-4 w-4" />
                <span>Usuários</span>
              </button>
            )}
          </div>
        </nav>

        {/* Main Content */}
        <main>
          {currentView === 'dashboard' && (
            <Dashboard samples={samples} auditLog={auditLog} user={user} />
          )}
          {currentView === 'samples' && (
            <SampleList 
              samples={samples} 
              setSamples={setSamples}
              setAuditLog={setAuditLog}
              user={user}
            />
          )}
          {currentView === 'add-sample' && (
            <SampleManager 
              samples={samples}
              setSamples={setSamples}
              setAuditLog={setAuditLog}
              user={user}
              onSuccess={() => setCurrentView('samples')}
            />
          )}
          {currentView === 'reports' && (
            <Reports 
              samples={samples}
              auditLog={auditLog}
              user={user}
            />
          )}
          {currentView === 'audit' && (
            <AuditLog auditLog={auditLog} />
          )}
          {currentView === 'users' && user.role === 'admin' && (
            <UserManager 
              user={user}
              setAuditLog={setAuditLog}
            />
          )}
        </main>
      </div>
    </div>
  );
}

export default App;
