import React, { useState, useEffect } from 'react';
import { User } from '../types';
import { saveToStorage, getFromStorage, addAuditEntry } from '../utils/storage';
import { UserPlus, Mail, Phone, Calendar, Building, Save, X, Edit, Trash2, CheckCircle, XCircle } from 'lucide-react';

interface UserManagerProps {
  user: User;
  setAuditLog: (log: any[]) => void;
}

const UserManager: React.FC<UserManagerProps> = ({ user, setAuditLog }) => {
  const [users, setUsers] = useState<User[]>([]);
  const [showForm, setShowForm] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [emailSent, setEmailSent] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    birthDate: '',
    workplace: '',
    role: 'technician' as 'admin' | 'researcher' | 'technician'
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = () => {
    const storedUsers = getFromStorage('nb3_all_users', []);
    setUsers(storedUsers);
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.name.trim()) {
      newErrors.name = 'Nome é obrigatório';
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email é obrigatório';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'Email inválido';
    } else if (users.some(u => u.email === formData.email && u.id !== editingUser?.id)) {
      newErrors.email = 'Email já cadastrado';
    }

    if (!formData.phone.trim()) {
      newErrors.phone = 'Telefone é obrigatório';
    }

    if (!formData.birthDate) {
      newErrors.birthDate = 'Data de nascimento é obrigatória';
    }

    if (!formData.workplace.trim()) {
      newErrors.workplace = 'Local de trabalho é obrigatório';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const generateUsername = (name: string, email: string) => {
    const namePart = name.toLowerCase().split(' ')[0];
    const emailPart = email.split('@')[0];
    return `${namePart}.${emailPart}`.replace(/[^a-z0-9.]/g, '');
  };

  const generateRegistrationToken = () => {
    return Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    const registrationToken = generateRegistrationToken();
    const username = generateUsername(formData.name, formData.email);

    const userData: User = {
      id: editingUser?.id || Date.now().toString(),
      username: editingUser?.username || username,
      name: formData.name,
      email: formData.email,
      phone: formData.phone,
      birthDate: formData.birthDate,
      workplace: formData.workplace,
      role: formData.role,
      isActive: true,
      hasPassword: editingUser?.hasPassword || false,
      registrationToken: editingUser?.registrationToken || registrationToken
    };

    let newUsers;
    let action;

    if (editingUser) {
      newUsers = users.map(u => u.id === editingUser.id ? userData : u);
      action = `Usuário ${userData.name} atualizado`;
    } else {
      newUsers = [...users, userData];
      action = `Novo usuário ${userData.name} cadastrado`;
      
      // Simulate email sending
      setEmailSent(userData.email!);
      setTimeout(() => setEmailSent(null), 5000);
    }

    setUsers(newUsers);
    saveToStorage('nb3_all_users', newUsers);
    
    const auditEntry = addAuditEntry(action, user.username);
    setAuditLog(prev => [...prev, auditEntry]);

    resetForm();
  };

  const resetForm = () => {
    setFormData({
      name: '',
      email: '',
      phone: '',
      birthDate: '',
      workplace: '',
      role: 'technician'
    });
    setErrors({});
    setShowForm(false);
    setEditingUser(null);
  };

  const handleEdit = (userToEdit: User) => {
    setFormData({
      name: userToEdit.name,
      email: userToEdit.email || '',
      phone: userToEdit.phone || '',
      birthDate: userToEdit.birthDate || '',
      workplace: userToEdit.workplace || '',
      role: userToEdit.role
    });
    setEditingUser(userToEdit);
    setShowForm(true);
  };

  const handleDelete = (userId: string) => {
    const userToDelete = users.find(u => u.id === userId);
    if (!userToDelete) return;

    const newUsers = users.filter(u => u.id !== userId);
    setUsers(newUsers);
    saveToStorage('nb3_all_users', newUsers);
    
    const auditEntry = addAuditEntry(`Usuário ${userToDelete.name} removido`, user.username);
    setAuditLog(prev => [...prev, auditEntry]);
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const getEmailContent = (userData: User) => {
    return `
Olá ${userData.name},

Você foi cadastrado no Sistema NB3 - Laboratório de Biossegurança.

Seus dados de acesso:
- Usuário: ${userData.username}
- Link para cadastrar senha: ${window.location.origin}/register?token=${userData.registrationToken}

Por favor, acesse o link acima para definir sua senha e ativar sua conta.

Atenciosamente,
Equipe do Sistema NB3
    `;
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white rounded-lg shadow p-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <UserPlus className="h-6 w-6 text-blue-600" />
            <div>
              <h2 className="text-xl font-semibold text-gray-900">Gerenciar Usuários</h2>
              <p className="text-gray-600 text-sm">Cadastro e gerenciamento de usuários do sistema</p>
            </div>
          </div>
          <button
            onClick={() => setShowForm(true)}
            className="flex items-center space-x-2 bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
          >
            <UserPlus className="h-4 w-4" />
            <span>Novo Usuário</span>
          </button>
        </div>
      </div>

      {/* Email Sent Notification */}
      {emailSent && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
          <div className="flex items-center space-x-2">
            <CheckCircle className="h-5 w-5 text-green-600" />
            <div>
              <p className="text-green-800 font-medium">Email enviado com sucesso!</p>
              <p className="text-green-700 text-sm">
                Um email foi enviado para {emailSent} com as instruções de cadastro.
              </p>
            </div>
          </div>
          <div className="mt-3 bg-white border border-green-200 rounded p-3">
            <p className="text-xs text-gray-600 font-medium mb-2">Conteúdo do email:</p>
            <pre className="text-xs text-gray-700 whitespace-pre-wrap">
              {getEmailContent(users.find(u => u.email === emailSent)!)}
            </pre>
          </div>
        </div>
      )}

      {/* User Form */}
      {showForm && (
        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">
              {editingUser ? 'Editar Usuário' : 'Novo Usuário'}
            </h3>
          </div>

          <form onSubmit={handleSubmit} className="p-6 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Nome Completo *
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => handleInputChange('name', e.target.value)}
                  className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                    errors.name ? 'border-red-300' : 'border-gray-300'
                  }`}
                  placeholder="Ex: João Silva"
                />
                {errors.name && <p className="text-red-500 text-xs mt-1">{errors.name}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Email *
                </label>
                <div className="relative">
                  <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="email"
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    className={`w-full pl-10 pr-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                      errors.email ? 'border-red-300' : 'border-gray-300'
                    }`}
                    placeholder="joao.silva@exemplo.com"
                  />
                </div>
                {errors.email && <p className="text-red-500 text-xs mt-1">{errors.email}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Telefone *
                </label>
                <div className="relative">
                  <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="tel"
                    value={formData.phone}
                    onChange={(e) => handleInputChange('phone', e.target.value)}
                    className={`w-full pl-10 pr-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                      errors.phone ? 'border-red-300' : 'border-gray-300'
                    }`}
                    placeholder="(11) 99999-9999"
                  />
                </div>
                {errors.phone && <p className="text-red-500 text-xs mt-1">{errors.phone}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Data de Nascimento *
                </label>
                <div className="relative">
                  <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="date"
                    value={formData.birthDate}
                    onChange={(e) => handleInputChange('birthDate', e.target.value)}
                    className={`w-full pl-10 pr-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                      errors.birthDate ? 'border-red-300' : 'border-gray-300'
                    }`}
                  />
                </div>
                {errors.birthDate && <p className="text-red-500 text-xs mt-1">{errors.birthDate}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Local de Trabalho *
                </label>
                <div className="relative">
                  <Building className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="text"
                    value={formData.workplace}
                    onChange={(e) => handleInputChange('workplace', e.target.value)}
                    className={`w-full pl-10 pr-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                      errors.workplace ? 'border-red-300' : 'border-gray-300'
                    }`}
                    placeholder="Ex: Hospital das Clínicas"
                  />
                </div>
                {errors.workplace && <p className="text-red-500 text-xs mt-1">{errors.workplace}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Função
                </label>
                <select
                  value={formData.role}
                  onChange={(e) => handleInputChange('role', e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="technician">Técnico</option>
                  <option value="researcher">Pesquisador</option>
                  <option value="admin">Administrador</option>
                </select>
              </div>
            </div>

            <div className="flex justify-end space-x-3 pt-6 border-t border-gray-200">
              <button
                type="button"
                onClick={resetForm}
                className="flex items-center px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition-colors"
              >
                <X className="h-4 w-4 mr-2" />
                Cancelar
              </button>
              <button
                type="submit"
                className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
              >
                <Save className="h-4 w-4 mr-2" />
                {editingUser ? 'Atualizar' : 'Cadastrar'}
              </button>
            </div>
          </form>
        </div>
      )}

      {/* Users List */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Usuários Cadastrados</h3>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Nome
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Email
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Telefone
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Local de Trabalho
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Função
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Ações
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {users.map((userItem) => (
                <tr key={userItem.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900">{userItem.name}</div>
                    <div className="text-xs text-gray-500">@{userItem.username}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {userItem.email}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {userItem.phone}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {userItem.workplace}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`inline-flex px-2 py-1 rounded-full text-xs font-medium ${
                      userItem.role === 'admin' ? 'text-purple-700 bg-purple-100' :
                      userItem.role === 'researcher' ? 'text-blue-700 bg-blue-100' :
                      'text-green-700 bg-green-100'
                    }`}>
                      {userItem.role === 'admin' ? 'Admin' : 
                       userItem.role === 'researcher' ? 'Pesquisador' : 'Técnico'}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center space-x-1">
                      {userItem.hasPassword ? (
                        <CheckCircle className="h-4 w-4 text-green-600" />
                      ) : (
                        <XCircle className="h-4 w-4 text-red-600" />
                      )}
                      <span className={`text-xs ${userItem.hasPassword ? 'text-green-700' : 'text-red-700'}`}>
                        {userItem.hasPassword ? 'Ativo' : 'Pendente'}
                      </span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <div className="flex justify-end space-x-2">
                      <button
                        onClick={() => handleEdit(userItem)}
                        className="text-blue-600 hover:text-blue-900 p-1"
                        title="Editar"
                      >
                        <Edit className="h-4 w-4" />
                      </button>
                      <button
                        onClick={() => handleDelete(userItem.id)}
                        className="text-red-600 hover:text-red-900 p-1"
                        title="Excluir"
                      >
                        <Trash2 className="h-4 w-4" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>

          {users.length === 0 && (
            <div className="text-center py-12">
              <UserPlus className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-500">Nenhum usuário cadastrado</p>
              <p className="text-gray-400 text-sm mt-2">
                Clique em "Novo Usuário" para começar
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default UserManager;
